const textureLoader = new THREE.TextureLoader()

const genorateGlobe = () =>
{
    
    globeGeometry = new THREE.SphereBufferGeometry(sphere_parameters.radius, sphere_parameters.point_number * 150, sphere_parameters.point_number * 150)
    
    for (let i = 0; i < globeGeometry.attributes.position.count; i++) {
        //color.set(0x19FF83); // green (XRPL) 
        //color.set(0x0F72E5); // blue (XRPL)
        color.set(new THREE.Color(sphere_parameters.color))
        //color.set(0xffffff * Math.random()); // blue (XRPL)

        //color.set(0xffffff);
        color.toArray(colors, i * 3)
    }
    globeGeometry.setAttribute('aColor', new THREE.BufferAttribute(new Float32Array(colors), 3))


    textureLoader.setCrossOrigin('')
    const texture = textureLoader.load('./textures/earthspec1k.jpg')
    texture.wrapS = THREE.RepeatWrapping
    texture.wrapT = THREE.RepeatWrapping
    texture.repeat.set(1, 1)
    // const disk = textureLoader.load('./textures/circle.png')
    //const disk = textureLoader.load('/textures/ripple-white.png')
    // const disk = textureLoader.load('/textures/circle.png')
    const disk = textureLoader.load('/textures/xrp.png')


    const mul = (mapControls.contained == true) ? 0.08: 0.1
    const globePointsShader = new THREE.ShaderMaterial({
        vertexColors: THREE.VertexColors,
        uniforms: {
            uVisibility: {
                value: texture
            },
            uShift: {
                value: 0
            },
            uShape: {
                value: disk
            },
            uSize: {
                value: sphere_parameters.point_size * mul
            },
            uScale: {
                value: sphere_parameters.point_scale
            }
        },
        vertexShader: globePointsVertexShader,
        fragmentShader: globePointsFragmentShader,
        transparent: true
    })
    globePoints.push(globePointsShader)
    // add the points to the sphere
    const pointsGlobeShader = new THREE.Points(globeGeometry, globePointsShader)
    pointsGlobeShaders.push(pointsGlobeShader)
    scene.add(pointsGlobeShader)
    

    innGlobeColor = new THREE.MeshPhongMaterial({
        roughness: 0.3,
        wireframe: false,
        emissive : (0x000000 * 0.25),
        opacity: 0.9,
        transparent: true
    })
    //innGlobeColor.color =  new THREE.Color('white')

    innerGlobe = new THREE.Mesh(
        sphereInner,
        innGlobeColor
    )
    if (mapControls.innerGlobe) {
        scene.add(innerGlobe)
    }
}

genorateGlobe()


/**
 * Starfield
 */

const starfield_parameters = {}
starfield_parameters.count = 1000
starfield_parameters.size = 0.02
starfield_parameters.radius = 10
starfield_parameters.insideColor = '#ffffff'
starfield_parameters.outsideColor = '#1b3984'

const genorateStarfield = () => 
{   
    const geometry = new THREE.BufferGeometry()
    const positions = new Float32Array(starfield_parameters.count * 3)
    
    const colors = new Float32Array(starfield_parameters.count * 3)
    const colorInside = new THREE.Color(starfield_parameters.insideColor)
    const colorOutside = new THREE.Color(starfield_parameters.outsideColor)

    for (var i = 0; i < starfield_parameters.count; i++) 
    {
        const radius = Math.random() * starfield_parameters.radius

        const i3 = i * 3
        positions[i3] = (Math.random() - 0.5) * 8000
        positions[i3 + 1] = (Math.random() - 0.5) * 8000
        positions[i3 + 2] = (Math.random() - 0.5) * 8000

                // Color
        const mixedColor = colorInside.clone()
        mixedColor.lerp(colorOutside, radius / starfield_parameters.radius)
        
        colors[i3    ] = mixedColor.r
        colors[i3 + 1] = mixedColor.g
        colors[i3 + 2] = mixedColor.b

    }


    geometry.setAttribute('position', new THREE.BufferAttribute(positions, 3))
    geometry.setAttribute('color', new THREE.BufferAttribute(colors, 3))

    /**
     * Material
     */
    const material = new THREE.PointsMaterial({
        size: starfield_parameters.size,
        sizeAttenuation: true,
        depthWrite: false,
        blending: THREE.AdditiveBlending,
        vertexColors: true
    })

    /**
     * Points
     */
    points = new THREE.Points(geometry, material)

    scene.add(points)
}


genorateStarfield()

const flows = []

const genorateFlow = (exchange, color, l1, l2, l3, l4) => {
    const pinAnimationShaderStart = new THREE.ShaderMaterial({
        uniforms: {
            uTime: { value: 0 },
            uColor: { value: new THREE.Color(color) }
        },
        transparent: true,
        vertexShader: pinAnimationVertexShader,
        fragmentShader: pinAnimationFragmentShader,
        blending: THREE.AdditiveBlending,
        side: THREE.DoubleSide
    })
    flows.push(pinAnimationShaderStart)

    const pinAnimationShaderEnd = new THREE.ShaderMaterial({
        uniforms: {
            uTime: { value: 0 },
            uColor: { value: new THREE.Color(color) }
        },
        transparent: true,
        vertexShader: pinAnimationVertexShader,
        fragmentShader: pinAnimationFragmentShader,
        blending: THREE.AdditiveBlending,
        side: THREE.DoubleSide
    })
    flows.push(pinAnimationShaderEnd)

    const laminatedFlowShader = new THREE.ShaderMaterial({
        uniforms: {
            uTime: { value: 0 },
            uColor: { value: new THREE.Color(color) }
        },
        //side: THREE.DoubleSide,
        transparent: true,
        vertexShader: laminatedFlowVertexShader,
        fragmentShader: laminatedFlowFragmentShader,
        blending: THREE.AdditiveBlending
    })
    flows.push(laminatedFlowShader)

    const flow_point = new THREE.CircleGeometry( 5, 25 )
    const startFlow = new THREE.Mesh(
        flow_point,
        pinAnimationShaderStart
    )
    
    scene.add(startFlow)

    const endFlow = new THREE.Mesh(
        flow_point,
        pinAnimationShaderEnd
    )
    endFlow.lookAt(new THREE.Vector3())
    scene.add(endFlow)


    function coordinateToPosition(lat, lng, radius) {
        let point = null

        const DEGREE_TO_RADIAN = Math.PI / 180;
        const phi = ( 90 - lat ) * DEGREE_TO_RADIAN;
        const theta = ( lng + 180 ) * DEGREE_TO_RADIAN;

        point = new THREE.Vector3(
            - radius * Math.sin( phi ) * Math.cos( theta ),
            radius * Math.cos( phi ),
            radius * Math.sin( phi ) * Math.sin( theta )
        )
        return point
    }


    const v1 = coordinateToPosition(l1, l2, sphere_parameters.radius)
    let v2 = coordinateToPosition(l3, l4, sphere_parameters.radius)

    startFlow.position.set( v1.x, v1.y, v1.z )
    endFlow.position.set( v2.x, v2.y, v2.z )
    startFlow.lookAt(new THREE.Vector3())
    endFlow.lookAt(new THREE.Vector3())


    const flow_points = getCurve( v1, v2 )

    function getCurve(v1, v2) {
        let points = []
        const height = Math.random() 
        for (var i = 0; i <= 20; i++) {
            const p = new THREE.Vector3().lerpVectors( v1, v2, i/20 )

            // normalize clamps it to sphere shape
            p.normalize()

            // multiplyScalar lofts the tube
            p.multiplyScalar(sphere_parameters.radius + (height * 100) *  Math.sin( Math.PI*i/20 ))
            points.push(p)
        }
        return points
    }

    const path_tube = new THREE.CatmullRomCurve3( flow_points )
    // radius controls the width of the 'line' tube
    const geometry_tube = new THREE.TubeGeometry( path_tube, 36, 1, 8, false )
    const mesh_flow = new THREE.Mesh( geometry_tube, laminatedFlowShader )

    scene.add( mesh_flow )


    if (mapControls.stackData == false)  {
        setTimeout(function() {
            flow_point.dispose()
            geometry_tube.dispose()
            pinAnimationShaderStart.dispose()
            pinAnimationShaderEnd.dispose()
            laminatedFlowShader.dispose()
            scene.remove(startFlow)
            scene.remove(endFlow)
            scene.remove(mesh_flow)
        }, 5000)
    }
    else {
        mapControls.dispose.push(flow_point)
        mapControls.dispose.push(geometry_tube)
        mapControls.dispose.push(pinAnimationShaderStart)
        mapControls.dispose.push(pinAnimationShaderEnd)
        mapControls.dispose.push(laminatedFlowShader)
        mapControls.remove.push(startFlow)
        mapControls.remove.push(endFlow)
        mapControls.remove.push(mesh_flow)    
    }
}

function checkAccess() {
    if (access == null) {
        if (localStorage.getItem('user_session')) {
            try {
                const user_session = JSON.parse(localStorage.getItem('user_session'))
                
                console.log('user session loaded from localStorage')
                if (user_session.uuid.length == 36 && user_session.expire == false) {
                    if (user_session.whale_watch === 1) {
                        access = true
                        return true    
                    }
                }
            } catch(e) {
                console.log(e)
            }
        }
        access = false
        return false
    }
    return access
}

let websocketRetrys = 0
const connectWebsocket = () => {
    const self = this
    const key = cors
    const socket = new WebSocket(socketURL)
    websocketRetrys++
    socket.onopen = function (message) {
        socket.send(JSON.stringify({
            request: 'SUBSCRIBE',
            message: key,
            channel: 'trade'
        }))

        socket.send(JSON.stringify({
            request: 'SUBSCRIBE',
            message: key,
            channel: 'xrpl'
        }))
        
        console.log("Map Connected to threexrp! :)")
    }

    //Called when the websocket receives a message.
    socket.onmessage = function (message) {
        if (mapControls.pauseMapData == false) {
            const data = JSON.parse(message.data)
            // XRP fiat trade
            if (data.trade != null) {
                if (mapControls.fiat) {
                    const exchange_machine_name = data.trade.e.replace("-", "_")
                    addExchangeGUI(exchange_machine_name)

                    if (data.trade.g == null) { return }
                    if (mapControls.minimum <= (data.trade.a )) {
                         
                        genorateFlow(
                            exchange_machine_name,
                            data.trade.c,
                            data.trade.g.o[0], 
                            data.trade.g.o[1], 
                            data.trade.g.d[0], 
                            data.trade.g.d[1]
                        )
                    }
                }
            }

            // XRPL payment
            if (data.xrpl != null) {
                if (mapControls.pauseXRP == false) {
                    // we have xrpl message
                    if (data.xrpl.geo == null) { return }
                    if (mapControls.minimum <= (data.xrpl.amount)) {
                    genorateFlow(
                        data.xrpl.source_name,
                        0x19FF83,
                        data.xrpl.geo.origin[0], 
                        data.xrpl.geo.origin[1], 
                        data.xrpl.geo.destination[0], 
                        data.xrpl.geo.destination[1])
                    }
                }
            }
        }
    }

    socket.onerror = function (message) {
        console.log("There was an error connection to websocket on threexrp! :(")
        console.log(message)
        socket.close()
    }

    
    socket.onclose = function (message) {
        setTimeout(function() {
            if (websocketRetrys <= maxRetrys) {
                connectWebsocket()    
            }
        }, 2000)
    }
}

connectWebsocket()

