/**
 * Sizes
 */
let sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

if (mapControls.contained == true) {
    sizes.height = 600
}


/**
 * Camera
 */
// Base camera
const camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height, 0.1, 20000)
camera.position.x = 0
camera.position.y = 0
camera.position.z = sphere_parameters.radius + 500
scene.add(camera)

// Controls
const controls = new OrbitControls(camera, canvas)
controls.enableDamping = true
controls.enableZoom = mapControls.zoom

/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas,
    alpha: true,
    powerPreference: 'high-performance',
    antialias: true
})

//scene.background = new THREE.Color( 0x151217 )
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))

/**
 * Animate
 */
const clock = new THREE.Clock()

/**
 * statistics
 */
const stats = new Stats()
//stats.showPanel(0) // 0: fps, 1: ms, 2: mb, 3+: custom
//document.body.appendChild(stats.dom)

const tick = ()  => {
    mapControls.rotateSpeed = (canvas.getAttribute('rotate') == 'true') ? 1.6 : 0
    
    let delta = clock.getDelta()
    const elapsedTime = clock.getElapsedTime()
    controls.autoRotate = (mapControls.rotateSpeed > 0) ? true : false
    controls.autoRotateSpeed = mapControls.rotateSpeed
    // Update controls
    controls.update()

    //update material
    for (var i = 0; i < flows.length; i++) {
        if (flows[i] !== 'undefined') {
            flows[i].uniforms.uTime.value += 25
        }
    }
    
    window.requestAnimationFrame(tick)

    renderer.render(scene, camera)
    // stats.end()
}

tick()

const onWindowResize = () => {
    
    if (mapControls.contained == true) {
        // renderer.setSize( 600, (600) )
        // camera.aspect = 600 / (600)
    }
    else {
        renderer.setSize( window.innerWidth, window.innerHeight )
        camera.aspect = window.innerWidth / window.innerHeight

        // update sizes
        sizes.width = window.innerWidth
        sizes.height = window.innerHeight

        camera.updateProjectionMatrix()
    }

    
}
